.loaderPrijava {
  width: 30rem;
  height: 4rem;
  margin: 0 auto;
  z-index: 2;
  background-color: #fff;
  position: relative;
  margin-top: 2rem;
  margin-bottom: 0.5rem;

  span {
    width: 4rem;
    height: 4rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
